import React, { Component } from "react";
import Slider from "react-slick";
import SlickCard from "../elements/Cardforsleek";
import CardMedia from '@material-ui/core/CardMedia';
// import "~slick-carousel/slick/slick.css"; 



import slide01 from "../../../src/image/19.jpg";
import slide02 from "../../../src/image/6.jpg";
import slide03 from "../../../src/image/21.jpg";

export default class Responsiver extends Component {
  render() {
    var settings = {
      dots: true,
      infinite: false,
      speed: 500,
      slidesToShow: 4,
      
      slidesToScroll: 4,
      initialSlide: 0,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 4,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        },
      ]
    };
    return (
      <div>
           <h2>   </h2>
            <h2 >.... VARIETIES </h2>
        <Slider {...settings}>
          <div>
            <SlickCard>
            <img src= {slide01}/>
            <h3></h3>
</SlickCard>
          </div>
          <div>
          <SlickCard>
            <img src= {slide03}/>
            <h3></h3>
</SlickCard>
            <h3>2</h3>
          </div>
          <div>
            <SlickCard>
            <img src= {slide01}/>
            <h3></h3>
</SlickCard>
          </div>
          <div>
            <h3>4</h3>
          </div>
          <div>
            <h3>5</h3>
          </div>
          <div>
            <h3>6</h3>
          </div>
          <div>
            <h3>7</h3>
          </div>
          <div>
            <h3>8</h3>
          </div>
        </Slider>
      </div>
    );
  }
}